import React from 'react';
import {ReactComponent as DropDownArrow} from '../../assets/images/qna/drop-down-arrow.svg';

import './QnA.scss';

const QnA = () => {
    const [answer, setAnswer] = React.useState(null);
    const handleCheckboxChange = (id) => {
        setAnswer((prevAnswer) => (prevAnswer === id ? null : id));
    };
    return (
        <div className='qna | section-wrapper'>
            <div className='content-wrapper'>
                <div className='section-header text-neutral-400'>
                    QnA
                </div>
                <ul className='qna__body | font-weight-light text-neutral-400'>
                    <li>
                        <input className="qna__checkbox" id="checkbox-1"
                               type="checkbox"
                               checked={answer === "answer-1"}
                               onChange={() => handleCheckboxChange("answer-1")}/>
                        <label htmlFor="checkbox-1" className="qna__question font-size-500">
                                  ?
                            <DropDownArrow className='drop-down-arrow'/>
                        </label>
                        <div className="qna__answer">
                            <p className="font-size-400">
                                  ,     -    ,
                                &nbsp;         
                                (),
                                      ,        
                                ,
                                       .
                            </p>
                        </div>
                    </li>
                    <li>
                        <input className="qna__checkbox" id="checkbox-2"
                               type="checkbox"
                               checked={answer === "answer-2"}
                               onChange={() => handleCheckboxChange("answer-2")}/>
                        <label htmlFor="checkbox-2" className="qna__question font-size-500">
                                 ?
                            <DropDownArrow className='drop-down-arrow'/>
                        </label>
                        <div className="qna__answer">
                            <p className="font-size-400">
                                  ,     -    ,
                                &nbsp;         
                                (),
                                      ,        
                                ,
                                       .
                            </p>
                        </div>
                    </li>
                    <li>
                        <input className="qna__checkbox" id="checkbox-3"
                               type="checkbox"
                               checked={answer === "answer-3"}
                               onChange={() => handleCheckboxChange("answer-3")}/>
                        <label htmlFor="checkbox-3" className="qna__question font-size-500">
                               ,    . &nbsp;?
                            <DropDownArrow className='drop-down-arrow'/>
                        </label>
                        <div className="qna__answer">
                            <p className="font-size-400">
                                  ,     -    ,
                                &nbsp;         
                                (),
                                      ,        
                                ,
                                       .
                            </p>
                        </div>
                    </li>
                    <li>
                        <input className="qna__checkbox" id="checkbox-4"
                               type="checkbox"
                               checked={answer === "answer-4"}
                               onChange={() => handleCheckboxChange("answer-4")}/>
                        <label htmlFor="checkbox-4" className="qna__question font-size-500">
                                  ?
                            <DropDownArrow className='drop-down-arrow'/>
                        </label>
                        <div className="qna__answer">
                            <p className="font-size-400">
                                  ,     -    ,
                                &nbsp;         
                                (),
                                      ,        
                                ,
                                       .
                            </p>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
    );
};
export default QnA;